@import url('../../colors.less');
#resume{
    display: flex;
    align-items: flex-start;
}

.resumeItem{
    text-decoration: none;
    color: @color-black;
    border-radius: 5px;
    margin:5px;
    background-color: @color-beige;
    padding: 10px;
    display: flex;
    align-items: center;

    -webkit-box-shadow: 0 0 6px 1px #505050;
    -moz-box-shadow: 0 0 6px 1px #505050;
    box-shadow: 0 0 6px 1px #505050;

    cursor: pointer;
    &:hover{
        background-color: @color-beige-dark;
    }
    img{
        max-width: 80px;
    }
}