@import url('../../colors.less');
#experience{
    display: grid;
    grid-template-columns: 1fr 1fr;
    >div{
        img{
            max-width: 550px;
        }
    }
}

.experienceText{
    border-radius: 5px;
    margin-bottom: 40px;
    padding: 20px;
    background-color: @color-beige;
    text-align: left;
    -webkit-box-shadow: 0 0 6px 1px #505050;
    -moz-box-shadow: 0 0 6px 1px #505050;
    box-shadow: 0 0 6px 1px #505050;
    h3{
        display: inline-block;
        font-size: 28px;
        margin-right: 20px;
    }
    p{
        font-size: 20px;
    }
}

.workingDate{
    background-color: #d9eee9;
    color: #34544a;
    padding: 5px 12px;
    font-size: 24px;
}