@import url('../colors.less');
.rsidebar{
    position: relative;
    padding: 7px;
    flex: 0 0 10%;
    background-image: url('../../images/binding_dark.png');
    -webkit-box-shadow: 0 0 8px 3px #999;
    -moz-box-shadow: 0 0 8px 3px #999;
    box-shadow: 0 0 8px 3px #999;
}
.tags{
    padding: 15px 0;
    h2{
        color: @color-beige;
        margin-bottom: 7px;
    }
    >div{
        a{
        color: @color-black;
            text-decoration: none;
            background-color: #eee;
            border-radius: 3px;
            display: inline-block;
            padding: 2px 7px;
            margin: 5px;
            &:hover{
                background-color: @color-beige-dark;

            }
        }
    }
}