.wrapper{
    text-align: left;
    box-sizing: border-box;
    padding: 50px!important;
    
    >div{
        background-color: #FFF8E4;
        padding: 40px 100px;
        border-radius: 10px;
        background-color: #fff;
        box-sizing: border-box;
        code{
            margin: 20px 0;
            border: 1px solid #e1e1e1;
        }

        h1{
            color: #a01d12;
            font-size: 44px;
        }
        p{
            font-size: 22px;
            padding: 10px 0;
            line-height: 32px;
            padding-right: 100px;
        }

        ul{
            font-size: 20px;
            padding-left: 30px;
        }
    }
}