@import url("../colors.less");
.sidebar{
  height: 100%;
  position: relative;
    max-width: 369px;
    -webkit-box-shadow: 0 0 9px 5px #000;
    -moz-box-shadow: 0 0 9px 5px #000;
    box-shadow: 0 0 9px 5px #000;
    flex: 0 0 20%;
    background-image: url(../../../public/images/bg_wood.jpg);
    h2{
        color: @color-beige;
        padding: 45px 0 20px 0;
        font-size: 32px;
    }

    &.closed{
        flex: auto;
        width: 90px;
        #burhan,#description, #message{
            display: none;
        }
        h2{
            margin-top: 50px;
            padding: 0;
            transform: rotate(-90deg);
        }
        #contact{
            position: absolute;
            bottom: 250px;
        }
        #sidebarCollapseBtn>div{
            transform: rotate(-180deg);
        }
        ul{
            margin-top: 50px;
        }
        ul li a{
            font-size:18px;
        }
    }
}

#burhan{
    margin: 0 auto;
    background-image: url(../../../public/images/burhan.jpg);
    width: 200px;
    height: 200px;
    background-size: cover;
    border-radius: 50%;
    border:3px solid @color-beige;
}

#sidebarCollapseBtn{
    background-image: url('../../images/screen.jpg');
    border-radius: 50%;
    width: 40px;
    height: 40px;
    position: absolute;
    top:50%;
    right:-20px;
    cursor:pointer;
    -webkit-box-shadow: -9px 0 8px 2px #000;
    -moz-box-shadow: -9px 0 8px 2px #000;
    box-shadow: -9px 0 8px 2px #000;
   >div{
    border-radius: 50%;
position: relative;
top: 6px;
left: 6px;
    background-color: @color-brown;
    background-image: url(../../../public/images/icons-arrow.png);
    background-repeat: no-repeat;
    width: 28px;
    height: 28px;
    background-size: 70%;
    background-position: center center;

    &:hover{
        background-color: @color-brown-dark;
    }
   }
}

#description{
    font-size: 24px;
    padding: 25px;
    // font-family: 'Arsenal-Italic';
    color: @color-beige;
}

#contact{
    color: @color-beige;
    i{
        font-size: 22px;
    }
    a{
        color: @color-beige;
        text-decoration: none;
        &:hover{
            text-decoration: underline;
        }
    }
}
#socs{
    margin-top: 10px;
    a{
        &:hover{
            text-decoration: none;
        }
    }
}
#message{
    padding-top: 10px;
    display: flex;
    justify-content: center;
    img{
        width: 24px;
        height: 24px;
        margin-right: 7px;
    }
   
    
}