@import url('../../colors.less');
.content{
    display: flex;
    flex-wrap: wrap;
    align-items: normal;
}

.caption{
    text-transform: uppercase;
    font-size: 24px;
    color: #34544a;
    margin-bottom: 10px;
    text-align: left;
}

.item{
    border-radius: 5px;
    margin:5px;
    background-color: @color-beige;
    padding: 10px;
    width: 40%;
    max-width: 500px;
    min-width: 450px;
    >div{
        display: flex;
        justify-content: space-between;
        margin-bottom: 7px;
        h4{
            background-color: #d9eee9;
            min-height: 25px;
            line-height: 25px;
            padding: 0 25px 0 10px;
            font-weight: normal;
        }
        ul{
            list-style: none;
            li{
                display: block;
                width: 20px;
                height: 25px;
                margin: 2px;
                display: block;
                float: left;
                background-color: #d9eee9;
                &[active] {
                    background-color: #84cdb7;
                }
            }
        }
    }
}