@import url('../colors.less');
.article{
    text-decoration: none;
    color: @color-black;
    flex: 0 0 31.333%;
    margin: 1%;
    -webkit-box-shadow: 0 0 6px 1px #505050;
    -moz-box-shadow: 0 0 6px 1px #505050;
    box-shadow: 0 0 6px 1px #505050;
    border-radius: 10px;
    overflow: hidden;
    background-color: @color-beige;
    img{
        width: 100%;
    }
}

.description
{
    padding: 15px;
    h3{
        margin-bottom: 8px;
    }
    p{
        font-size: 18px;
    }
}

.imageWrapper{
    width: 100%;
    img{
        width: 100%;

    }
}
@media only screen and (max-width: 1300px) {
    .article{
        flex: 0 0 48%;
        }
  }